import { Modal } from "@chac/common/components/Modal";
import React from "react";
import { useNavigate } from "react-router-dom";
import styled from 'styled-components'
const StyledContainer = styled.div`
	h1 {
		font-size: 1.6rem;
		font-weight: 400;
	}
	.pet-item {
		font-size: 1.2rem;
		background-color: #e2e2e2;
    	border-radius: 20px;
		display: flex;
		justify-content: space-between;
		margin: 15px 30px;
		padding: 15px;
		div.img {
			background-color: red;
			border-radius: 50%;
			width: 50px;
			height: 50px;
		}
	}
	
`
export default function SelectPetBottomSheet({data, close}) {
	const {petList, profile, setProfile} = data.props;
	const navigate = useNavigate();

	// event
	const closeHandler = (result) => {
		console.log(result)
		setProfile(result);
		close();
	}
	const petRegisterButtonClick = () => {
		close();
		navigate('/petRegister');

	}
	return (
		<Modal type="bottomSheet" close={close}>
			<StyledContainer>
				<h1>누구의 건강을 케어할까요?</h1>
				<h3>착! 라운지와 MY페이지에서는 대표로 선택한 아이의 정보가 표시되요</h3>
				{
					petList.map((item, index) => (
						<PetProfileItem key={index} {...item} onClick={() => closeHandler(item)}/>
					))
				}
				<PetRegisterButton onClick={petRegisterButtonClick}/>
				
				{/* <button onClick={closeHandler}>닫기</button> */}
			</StyledContainer>
		</Modal>
	);
}

function PetProfileItem ({img, name, onClick}) {
	return (
		<div className="pet-item" onClick={onClick}>
			<div className="img"> {img}</div>
			<span className="name">{name}</span> 
		</div>
	)
}

function PetRegisterButton ({onClick}) {
	return (
		<div className="pet-item" onClick={onClick}>
			+ 추가하기
		</div>
	)
}
